import { NavLink, useNavigate } from "react-router-dom";
import "../../styles/styles.scss";
import { startLogout } from "../../actions/auth";
import { useSelector, useDispatch } from "react-redux";

import {
    AppBar,
    Toolbar,
    CssBaseline,
    Typography,
    makeStyles,
    useTheme,
    useMediaQuery,
} from "@material-ui/core";

import DrawerComponent from "./Drawer";
import { memo } from "react";
const useStyles = makeStyles((theme) => ({
    abRoot: {
        backgroundColor: "#c55c5c",
    },

    navlinks: {
        marginLeft: theme.spacing(5),
        display: "flex",
    },
    logo: {
        flexGrow: "1",
        cursor: "pointer",
    },
    link: {
        textDecoration: "none",
        color: "white",
        fontSize: "20px",
        marginLeft: theme.spacing(5),
        "&:hover": {
            color: "yellow",
            borderBottom: "1px solid white",
        },
    },
}));

const Navbar = () => {
    const classes = useStyles();
    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down("md"));

    const navigate = useNavigate();
    const dispatch = useDispatch();
    const { uid, name } = useSelector((state) => state.auth);

    const handleLogout = () => {
        dispatch(startLogout());
    };

    return (
        <AppBar
            position="static"
            classes={{
                root: classes.abRoot,
            }}
        >            <CssBaseline />
            <Toolbar>
                <Typography variant="h4" className={classes.logo}>
                    Navbar
                </Typography>
                {isMobile ? (
                    <DrawerComponent />
                ) : (
                    <div className={classes.navlinks}>

                           <NavLink
                            to="/admin/nosotros"
                            className={({ isActive }) =>
                                "nav-item nav-link " +
                                (isActive ? "active" : "")
                            }
                        >
                            Nosotros
                        </NavLink>
                        <NavLink
                            to="/admin/usuarios"
                            className={({ isActive }) =>
                                "nav-item nav-link " +
                                (isActive ? "active" : "")
                            }
                        >
                            Usuarios
                        </NavLink>

                        <NavLink
                            to="/admin/bolsas"
                            className={({ isActive }) =>
                                "nav-item nav-link " +
                                (isActive ? "active" : "")
                            }
                        >
                            Bolsas
                        </NavLink>

                        <NavLink
                            to="/admin/blogs"
                            className={({ isActive }) =>
                                "nav-item nav-link " +
                                (isActive ? "active" : "")
                            }
                        >
                            Anuncios
                        </NavLink>

                        <NavLink
                            to="/admin/videos"
                            className={({ isActive }) =>
                                "nav-item nav-link " +
                                (isActive ? "active" : "")
                            }
                        >
                            Videos
                        </NavLink>

                        <NavLink
                            to="/admin/carrusel"
                            className={({ isActive }) =>
                                "nav-item nav-link " +
                                (isActive ? "active" : "")
                            }
                        >
                            Materiales
                        </NavLink>

                        <NavLink
                            to="/admin/categorias"
                            className={({ isActive }) =>
                                "nav-item nav-link " +
                                (isActive ? "active" : "")
                            }
                        >
                            Categorías
                        </NavLink>

                        <NavLink
                            to="/admin/register"
                            className={({ isActive }) =>
                                "nav-item nav-link " +
                                (isActive ? "active" : "")
                            }
                        >
                            Registrar
                        </NavLink>

                        <span className="nav-item nav-link text-info">
                            {name}
                        </span>

                        <button
                            className="nav-item nav-link btn"
                            onClick={handleLogout}
                        >
                            Logout
                        </button>
                    </div>
                )}
            </Toolbar>
        </AppBar>
    );
};
export default memo(Navbar);
