import { memo, useState } from "react";
import {
    Divider,
    Drawer,
    IconButton,
    List,
    ListItem,
    ListItemText,
    makeStyles,
} from "@material-ui/core";
import { useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { startLogout } from "../../actions/auth";
import MenuIcon from "@material-ui/icons/Menu";

const useStyles = makeStyles(() => ({
    link: {
        textDecoration: "none",
        color: "red",
        fontSize: "20px",
    },
    icon: {
        color: "white",
    },
}));

function DrawerComponent() {
    const dispatch = useDispatch();
    const handleLogout = () => {
        dispatch(startLogout());
    };

    const classes = useStyles();
    const [openDrawer, setOpenDrawer] = useState(false);
    return (
        <>
            <Drawer open={openDrawer} onClose={() => setOpenDrawer(false)}>
                <List>
            
                <ListItem onClick={() => setOpenDrawer(false)}>
                        <ListItemText>
                            <Link to="/admin/nosotros" className={classes.link}>
                                Nosotros
                            </Link>
                        </ListItemText>
                    </ListItem>


                    <Divider />
                <ListItem onClick={() => setOpenDrawer(false)}>
                        <ListItemText>
                            <Link to="/admin/usuarios" className={classes.link}>
                                Usuarios
                            </Link>
                        </ListItemText>
                    </ListItem>


                    <Divider />
                <ListItem onClick={() => setOpenDrawer(false)}>
                        <ListItemText>
                            <Link to="/admin/bolsas" className={classes.link}>
                                Bolsas
                            </Link>
                        </ListItemText>
                    </ListItem>


                    <Divider />



                    <ListItem onClick={() => setOpenDrawer(false)}>
                        <ListItemText>
                            <Link to="/admin/blogs" className={classes.link}>
                                Blogs
                            </Link>
                        </ListItemText>
                    </ListItem>


                    <Divider />


                    <ListItem onClick={() => setOpenDrawer(false)}>
                        <ListItemText>
                            <Link to="/admin/videos" className={classes.link}>
                                Videos
                            </Link>
                        </ListItemText>
                    </ListItem>


                    <Divider />
                    <ListItem onClick={() => setOpenDrawer(false)}>
                        <ListItemText>
                            <Link to="/admin/carrusel" className={classes.link}>
                                Materiales
                            </Link>
                        </ListItemText>
                    </ListItem>

                    <Divider />
                    <ListItem onClick={() => setOpenDrawer(false)}>
                        <ListItemText>
                            <Link to="/admin/categorias" className={classes.link}>
                                Categorías
                            </Link>
                        </ListItemText>
                    </ListItem>
                    <Divider />
                    <ListItem onClick={() => setOpenDrawer(false)}>
                        <ListItemText>
                            <Link to="/admin/register" className={classes.link}>
                                Registrar
                            </Link>
                        </ListItemText>
                    </ListItem>
                    <Divider />
                    <ListItem onClick={() => setOpenDrawer(false)}>
                        <ListItemText>

                            <button
                               className={classes.link}
                                onClick={handleLogout}
                            >
                                Logout
                            </button>
                        </ListItemText>
                    </ListItem>
                    <Divider />
                </List>
            </Drawer>
            <IconButton
                onClick={() => setOpenDrawer(!openDrawer)}
                className={classes.icon}
            >
                <MenuIcon />
            </IconButton>
        </>
    );
}
export default memo(DrawerComponent);
